// 封装vant方法
import { Toast } from 'vant';

/**
 * 开启 加载提示
 */
export function openLoading(){
  Toast.loading({
    message: '加载中...',
    forbidClick: true,
    loadingType: 'spinner',
    duration:0
  });
}

/**
 * 关闭 加载提示
 */
export function closeLoading(){
  Toast.clear()
}


/**
 * toast 提示
 */
export function openToast(message,timeOut){
  Toast({message,duration:timeOut})
}