/**
 * 获取权限code
 */
export function powerCode() {
  return new Promise((resolve, reject) => {
    // var corpId = getStore('corpId')
    var corpId = 'ding4a3ef860bd74563935c2f4657eb6378f'
    dd.ready(function () {
      dd.runtime.permission.requestAuthCode({
        corpId: corpId,
        onSuccess: function (result) {
          resolve([result, corpId]);
        },
        onFail: function (err) {
          console.log(err)
          reject(err);
        }
      });
    });
  });
}

/**
 * 导航栏
 */
export function headTop(name, rightBtn) {
  return new Promise((resolve, reject) => {
    dd.biz.navigation.setTitle({
      title: name, //控制标题文本，空字符串表示显示默认文本
      onSuccess: function (result) {
        if (rightBtn != undefined && rightBtn != '') {
          var rightBtns = rightBtn
          var show = true
        } else {
          var rightBtns = ''
          var show = false
        }
        dd.biz.navigation.setRight({
          show: show, //控制按钮显示， true 显示， false 隐藏， 默认true
          control: true, //是否控制点击事件，true 控制，false 不控制， 默认false
          text: rightBtns, //控制显示文本，空字符串表示显示默认文本
          onSuccess: function (result) {
            resolve(result);
          },
          onFail: function (err) { }
        });
      },
      onFail: function (err) { }
    });
  })
}

/**
 * 钉钉扫描二维码、条形码
 * type为qrCode(二维码)、barCode(条形码)、all(全部)。默认all，若有qrCode、barCode扫描不出来，请修改type为all
 */
export function sweepCode(typeName) {
  return new Promise((resolve, reject) => {
    if (typeName == '' && typeName == undefined) {
      typeName = all
    }
    dd.biz.util.scan({
      type: typeName,
      onSuccess: function (data) {
        resolve(data)
      },
      onFail: function (err) {
        reject(err)
      }
    });
  })
}

/**
 * 打开时间日历
 */
export function openDataTime() {
  return new Promise((resolve, reject) => {
    var currentTime = getTime(new Date().getTime()).substring(0, 10);
    dd.biz.util.datepicker({
      format: "yyyy-MM-dd", //注意：format只支持android系统规范，即2015-03-31格式为yyyy-MM-dd
      value: currentTime, //默认显示日期
      onSuccess: function (result) {
        resolve(result)
      },
      onFail: function (err) {
        reject(err)
      }
    });
  })
}

/**
 * 获取联系人
 */
export function getPople() {
  return new Promise((resolve, reject) => {
    console.log(getStore('_config'))
    dd.biz.contact.complexPicker({
      title: "测试标题", //标题
      corpId: _config.corpId, //企业的corpId
      multiple: true, //是否多选
      limitTips: "超出了", //超过限定人数返回提示
      maxUsers: 1000, //最大可选人数
      pickedUsers: [], //已选用户
      pickedDepartments: [], //已选部门
      disabledUsers: [], //不可选用户
      disabledDepartments: [], //不可选部门
      requiredUsers: [], //必选用户（不可取消选中状态）
      requiredDepartments: [], //必选部门（不可取消选中状态）
      appId: '32947', //微应用的Id
      permissionType: "xxx", //可添加权限校验，选人权限，目前只有GLOBAL这个参数
      responseUserOnly: false, //返回人，或者返回人和部门
      startWithDepartmentId: 0, //仅支持0和-1
      onSuccess: function (result) {
        resolve(result)
      },
      onFail: function (err) {
        console.log(err)
        reject(err)
      }
    });
  })
}

/**
 * 拨打电话
 */
export function callTel(tel) {
  return new Promise((resolve, reject) => {
    dd.biz.telephone.call({
      users: [tel], //用户列表，工号
      corpId: config.corpId, //企业id
      onSuccess: function () { },
      onFail: function () { }
    });
  })
}

/**
 * 获取当前地理位置
 */
export function getMap() {
  return new Promise((resolve, reject) => {
    dd.device.geolocation.get({
      targetAccuracy: Number,
      coordinate: Number,
      withReGeocode: Boolean,
      useCache: true, //默认是true，如果需要频繁获取地理位置，请设置false
      onSuccess: function (result) {
        resolve(result)
      },
      onFail: function (err) {
        reject(err)
      }
    });
  })
}

/**
 * 打开地图（可滑动地图选择当前地址）
 */
export function openMap(latitude, longitude) {
  return new Promise((resolve, reject) => {
    dd.biz.map.locate({
      latitude: latitude, // 纬度，非必须
      longitude: longitude, // 经度，非必须
      onSuccess: function (result) {
        resolve(result)
      },
      onFail: function (err) {
        this.common_vant.Toast(err)
      }
    });
  })
}

/**
 * 显示带标识的地图 (传入经纬度、地方名称)
 */
export function mapTitle(latitude, longitude, title) {
  return new Promise((resolve, reject) => {
    dd.biz.map.view({
      latitude: latitude, // 纬度
      longitude: longitude, // 经度
      title: title // 地址/POI名称
    });
  })
}

